export function musicReducer(state = {
  musics: [],
  fetching: false,
  fetched: false,
  error: null
}, action) {
  switch(action.type) {
    case 'FETCHING_MUSICS':
      return {...state, fetching: true};
    case 'FETCHED_MUSICS_OK':
      return {...state, fetching: false, fetched: true, musics: action.musics};
    case 'FETCHED_MUSICS_FAIL':
      return {...state, fetching: false, fetched: false, error: action.error};
    default:
      return state;
  }
}

export function currentRecordReducer(state = {
  music: null,
  isSet: false
}, action) {
  switch (action.type) {
    case "SET_CURRENT_RECORD":
      return {...state, music: action.music, isSet: true};
    default:
      return state;
  }
}



