import config from '../config';
import axios from 'axios';

export function fetchMusics() {
  return function(dispatch) {
    dispatch({
      type: 'FETCHING_MUSICS'
    });
    axios.get(`${config.API_URL}/musics`).then(res => {
      if (res.data.status === 'ok') {
        dispatch({
          type: 'FETCHED_MUSICS_OK',
          musics: res.data.musics
        });
      } else {
        dispatch({
          type: 'FETCHED_MUSICS_FAIL',
          error: res.data.error
        })
      }
    }).catch(err => {
      console.log(err);
    })
  }
}

export function setCurrentRecord(music) {
  return function(dispatch) {
    dispatch({
      type: 'SET_CURRENT_RECORD',
      music: music
    })
  }
}



