import { combineReducers } from 'redux';
import { collectionReducer } from './collectionReducers';
import { storyReducer, storyDetailReducer } from './storyReducers';
import { musicReducer, currentRecordReducer } from './musicReducers';

export default combineReducers({
  collectionState: collectionReducer,
  storyState: storyReducer,
  storyDetailState: storyDetailReducer,
  musicState: musicReducer,
  currentRecordState: currentRecordReducer
});



