import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchCollections } from '../actions/collectionActions';

import CollectionList from '../components/CollectionList';
import Spinner from '../components/Spinner';

function mapStateToProps(state) {
  return {
    collections: state.collectionState.collections,
    isFetching: state.collectionState.fetching,
    isFetched: state.collectionState.fetched
  }
}

function mapDispatchToProps(dispatch) {
  return {
    fetchCollections,
    dispatch
  }
}


class Home extends Component {
  constructor(props) {
    super(props);
  }
  componentWillMount() {
    this.props.dispatch(this.props.fetchCollections());
  }
  render() {
    if (this.props.isFetching) {
      return (
        <div className="nx-content">
          <Spinner />
        </div>
      )
    } else {
      return (
        <div className="nx-content">
          <CollectionList {...this.props} />
        </div>
      )
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);



