import config from '../config';
import axios from 'axios';

export function fetchStoriesByCollection(collection_slug, page) {
  return function(dispatch) {
    dispatch({
      type: 'FETCHING_STORIES'
    });
    axios.get(`${config.API_URL}/collection/${collection_slug}/stories?page=${page}`).then(res => {
      if (res.data.status === 'ok') {
        dispatch({
          type: 'FETCHED_STORIES_OK',
          stories: res.data.stories
        });
      } else {
        dispatch({
          type: 'FETCHED_STORIES_FAIL',
          error: res.data.error
        })
      }
    }).catch(err => {
      console.log(err);
    })
  }
}

export function showLightbox(story) {
  return function(dispatch) {
    dispatch({
      type: 'SHOW_LIGHTBOX',
      story: story
    })
  }
}

export function hideLightbox() {
  return function(dispatch) {
    dispatch({
      type: 'HIDE_LIGHTBOX'
    })
  }
}



