import React, { Component } from 'react';
import Spinner from './Spinner';

export default class RYPlayer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPlaying: false
    };
    this._playAudio = this._playAudio.bind(this);
    this._pauseAudio = this._pauseAudio.bind(this);
  }
  componentDidMount() {
    let { autoPlay } = this.props;
    this.setState({
      isPlaying: autoPlay
    });
  }
  _playAudio() {
    this.setState({
      isPlaying: !this.state.isPlaying
    });
    this.audioEle.play();
  }
  _pauseAudio() {
    this.setState({
      isPlaying: !this.state.isPlaying
    });
    this.audioEle.pause();
  }
  render() {
    let { currentRecord, autoPlay, loop, isSet } = this.props;

    if (!isSet) {
      return (
        <Spinner />
      )
    } else {
      let defaultStyle = {
        backgroundImage: `url(${currentRecord.cover_url})`
      }
      let playRender = this.state.isPlaying ? <div className="player-pause-button" onClick={this._pauseAudio}></div> : <div className="player-play-button" onClick={this._playAudio}></div>;
      let audioRender = '';
      if (autoPlay && loop) {
        audioRender = (
          <audio ref={(ref) => this.audioEle = ref} src={currentRecord.source_url} autoPlay loop></audio>
        )
      } else if (loop) {
        audioRender = (
          <audio ref={(ref) => this.audioEle = ref} src={currentRecord.source_url} loop></audio>
        )
      } else if (autoPlay) {
        audioRender = (
          <audio ref={(ref) => this.audioEle = ref} src={currentRecord.source_url} autoPlay></audio>
        )
      }
      return (
        <div className="ry-audio-player">
          <div className="player-cover" style={defaultStyle}></div>
          <div className={this.state.isPlaying ? 'player-record' : 'player-record is-paused'}>
            <div className="current-cover">
              <img src={currentRecord.cover_url} alt={currentRecord.song_name} />
              {playRender}
            </div>
          </div>
          <div className="player-info-wrap">
            <div className="current-record-title">{currentRecord.song_name}</div>
            <div className="current-record-singer">{currentRecord.singer_name}</div>
          </div>
          {audioRender}
        </div>
      )
    }
  }
}



