export function collectionReducer(state = {
  collections: [],
  fetching: false,
  fetched: false,
  error: null
}, action) {
  switch (action.type) {
    case "FETCHING_COLLECTIONS":
      return {...state, fetching: true};
    case "FETCHED_COLLECTIONS_OK":
      return {...state, fetching: false, fetched: true, collections: action.collections};
    case "FETCHED_COLLECTIONS_FAIL":
      return {...state, fetching: false, fetched: false, error: action.error};
    default:
      return state
  }
}



