import React, { Component } from 'react';

class RYAudioList extends Component {
  constructor(props) {
    super(props);
  }
  _changeSong(record) {
    this.props.dispatch(this.props.setCurrentRecord(record));
  }
  render() {
    let { records } = this.props;
    let list = records.map(item => {
      var style = {
        backgroundImage: `url(${item.cover_url})`
      }
      return (
        <div className="record-item" key={item.objectId} onClick={() => this._changeSong(item)}>
          <div className="record-cover" style={style}>
          </div>
          <div className="record-info">
            <div className="record-title">{item.song_name}</div>
            <div className="record-singer">{item.singer_name}</div>
          </div>
        </div>
      )
    })
    return (
      <div className="ry-player-list">
        {list}
      </div>
    )
  }
}

export default RYAudioList;



