import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchStoriesByCollection, showLightbox } from '../actions/storyActions';

import StoryList from '../components/StoryList';
import Spinner from '../components/Spinner';

import Lightbox from '../components/Lightbox';

function mapStateToProps(state) {
  return {
    stories: state.storyState.stories,
    fetching: state.storyState.fetching,
    fetched: state.storyState.fetched
  }
}

function mapDispatchToProps(dispatch) {
  return {
    fetchStoriesByCollection,
    showLightbox,
    dispatch
  }
}

class Collection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1
    };
  }
  componentWillMount() {
    let { slug } = this.props.params;
    let page = this.state.currentPage;
    this.props.dispatch(this.props.fetchStoriesByCollection(slug, page));
  }
  render() {
    if (!this.props.fetching) {
      return (
        <div className="nx-content">
          <StoryList {...this.props} />
          <Lightbox />
        </div>
      )
    } else {
      return (
        <div className="nx-content">
          <Spinner />
        </div>
      )
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Collection);



