export function storyReducer(state = {
  stories: [],
  fetching: false,
  fetched: false,
  error: null
}, action) {
  switch (action.type) {
    case 'FETCHING_STORIES':
      return {...state, fetching: true};
    case 'FETCHED_STORIES_OK':
      return {...state, fetching: false, fetched: true, stories: action.stories};
    case 'FETCHED_STORIES_FAIL':
      return {...state, fetching: false, fetched: false, error: action.error};
    default:
      return state;
  }
}

export function storyDetailReducer(state = {
  story: null,
  isShow: false
}, action) {
  switch (action.type) {
    case 'SHOW_LIGHTBOX':
      return {...state, story: action.story, isShow: true};
    case 'HIDE_LIGHTBOX':
      return {...state, isShow: false};
    default:
      return state;
  }
}



