import React, { Component } from 'react';
import { setCurrentRecord } from '../actions/musicActions';
import { connect } from 'react-redux';

import RYPlayer from './RYPlayer';
import RYAudioList from './RYAudioList';

import '../assets/ryaudio.css';

function mapStateToProps(state) {
  return {
    currentRecord: state.currentRecordState.music,
    isSet: state.currentRecordState.isSet
  }
}

function mapDispatchToProps(dispatch) {
  return {
    setCurrentRecord,
    dispatch
  }
}

class RYAudio extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    if (this.props.musics && this.props.musics.length > 0) {
      this.props.dispatch(this.props.setCurrentRecord(this.props.musics[0]))
    }
  }
  render() {
    return (
      <div className="ry-audio-container">
        <RYPlayer currentRecord={this.props.currentRecord} isSet={this.props.isSet} autoPlay={true} loop={true}/>
        <RYAudioList setCurrentRecord={this.props.setCurrentRecord} dispatch={this.props.dispatch} records={this.props.musics} />
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RYAudio);



