import React, { Component } from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import Spinner from './Spinner';

import { connect } from 'react-redux';
import { hideLightbox } from '../actions/storyActions';

import '../assets/lightbox.css';

function mapStateToProps(state) {
  return {
     storyDetail: state.storyDetailState.story,
     isShow: state.storyDetailState.isShow
  }
}

function mapDispatchToProps(dispatch) {
  return {
    hideLightbox,
    dispatch
  }
}


class Lightbox extends Component {
  hideStoryDetailLightbox() {
    this.props.dispatch(this.props.hideLightbox());
  }
  render() {
    let { storyDetail, isShow } = this.props;
    let content = storyDetail ? <img src={storyDetail.image_url} alt={storyDetail.description || 'lightbox image'} /> : <Spinner type="lightbox" />;
    return (
      <div className="nx-lightbox">
        <div className={isShow ? 'nx-lightbox-mask is-show': 'nx-lightbox-mask'} onClick={this.hideStoryDetailLightbox.bind(this)}>
           <div className="nx-lightbox-content">
             {content}
           </div>
        </div>
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Lightbox);



