import React, { Component } from 'react';

import { connect } from 'react-redux';
import { fetchMusics } from '../actions/musicActions';
import Spinner from '../components/Spinner';
import RYAudio from '../components/RYAudio';

function mapStateToProps(state) {
  return {
    musics: state.musicState.musics,
    fetching: state.musicState.fetching,
    fetched: state.musicState.fetched
  }
}

function mapDispatchToProps(dispatch) {
  return {
    fetchMusics,
    dispatch
  }
}

class Music extends Component {
  componentWillMount() {
    this.props.dispatch(this.props.fetchMusics());
  }
  render() {
    if (this.props.fetching) {
      return (
        <div className="nx-content">
          <Spinner />
        </div>
      )
    } else {
      return (
        <div className="nx-content">
          <div className="nx-music-zone">
            <RYAudio {...this.props} />
            <div className="rights-tip">This site is only for demonstration, please buy the genuine music</div>
          </div>
        </div>
      )
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Music);



