import React, { Component } from 'react';

export default class Story extends Component {
  showStoryDetailLightbox(story) {
    this.props.dispatch(this.props.showLightbox(story));
  }
  render() {
    let { story } = this.props;
    return (
      <div className="Story" onClick={() => {this.showStoryDetailLightbox(story)}}>
        <div className="story-overlay">
          <div className="story-content">
            <span>{story.description}</span>
          </div>
        </div>
        <img className="story-image" src={story.image_url} alt={story.description} />
      </div>
    )
  }
}



